
.modal {
    height:auto;
    top:auto;
    left:auto;
    bottom:auto;
    right:auto;
}

.answer{
    background:#F7F7F8;
    color: #597381;
    border-radius: 1em;
}

.low-balance{
    font-size: 20px;
    font-weight: 600;
    color: #6541B4;
}

.confirm .icon{
    font-size: 1.2em;
    color: #6541B4;
}

.ui.button {
    border-radius: 2em;
    border: 1px solid #6541B4;
    background: #fff;
    color: #6541B4;
}

.ui.button.add-money-button {
    background: #6541B4;
    color: #fff;
}

.balance{
    color: #6541B4;
    font-size: 18px;
}

.money-icon{
    font-size: 29px;
    padding-left: 5px;
    padding-bottom: 4px;
}


.contest {
    background: #fff;
    border-radius: 1.5em;
    line-height: 1.4;
}

.question{ 
    color:#6647B6;
    font-size: 22px;
    font-weight: 800;
}

.details {
    color: #597381;
    font-weight: 600;
}

.contest .icon{ 
    font-size: 27px;
    padding-left: 2px;
    padding-bottom: 4px;
    color:#6647B6;
}

.option-wrapper {
    background: #F7F7F8;
    border-radius: 1.5em;
    font-size: 18px;
    font-weight: 800;
    color: #597381;
}
